.navbar {
    position: sticky;
    top: 10px;
    width: 600px;
    height: 56px;
    margin-right: auto;
    margin-left: auto;
    border-radius: 15px;
    background-color: black;
    opacity: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    margin-bottom: -66px;
    z-index: 5;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 11px 9px 10px 5px rgba(0,0,0,0.4); 
    box-shadow: 0px 10px 13px -7px #000000, 11px 9px 10px 5px rgba(0,0,0,0.4);
  }
  
  .logo {
    width: 100px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 3.5px solid red;
    color: white;
  }

  .navbar_logo {
    width: 100%;
    height: 100%;
  }
  
  .navbar_logo:hover {
    cursor: pointer;
  }
  .navbar__list {
    display: flex;
    list-style: none;
  }
  
  .navbar__item {
    white-space: nowrap;
  }
  .navbar__link {
    font-size: 1rem;
    text-decoration: none;
    color: rgb(255, 255, 255);
    text-align: center;
    padding: 10px;
    margin: 0px 5px;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-color: transparent;
    transition: 250ms;
  }
  
  .navbar__link:hover {
    border-color: white;
    transition: 250ms;
    cursor: pointer;
  }

  .walletButton {
    width: 140px;
    height: 46px;
    margin-right: 10px;
    color: white;
    background-color: blue;
    border-radius: 15px;
    font-size: 1rem;
  }
  
